@tailwind base;
@tailwind components;
@tailwind utilities;

/* Custom CSS for IMS React App */

/* Test class to verify CSS is working */
.css-test {
  color: red;
  font-weight: bold;
}

/* Utility classes that might be missing */
.space-y-4 > :not([hidden]) ~ :not([hidden]) {
  --tw-space-y-reverse: 0;
  margin-top: calc(1rem * calc(1 - var(--tw-space-y-reverse)));
  margin-bottom: calc(1rem * var(--tw-space-y-reverse));
}

.space-y-6 > :not([hidden]) ~ :not([hidden]) {
  --tw-space-y-reverse: 0;
  margin-top: calc(1.5rem * calc(1 - var(--tw-space-y-reverse)));
  margin-bottom: calc(1.5rem * var(--tw-space-y-reverse));
}

/* Navigation Link Styles */
.nav-link {
  @apply flex items-center px-3 py-2 text-sm font-medium rounded-md transition-colors duration-200;
}

.nav-link-active {
  @apply bg-blue-100 text-blue-700;
}

.nav-link-inactive {
  @apply text-gray-600 hover:text-gray-900 hover:bg-gray-100;
}

/* Loading Spinner */
.loading-spinner {
  @apply animate-spin rounded-full border-b-2 border-blue-600;
}

/* Card Styles */
.card {
  @apply bg-white shadow rounded-lg;
}

.card-header {
  @apply px-4 sm:px-6 py-4 border-b border-gray-200;
}

.card-title {
  @apply text-lg font-medium text-gray-900;
}

/* Enhanced Card Styles */
.card-enhanced {
  @apply bg-white rounded-2xl shadow-lg border border-gray-100 overflow-hidden;
}

.card-enhanced-header {
  @apply p-6 border-b border-gray-100;
}

.card-enhanced-body {
  @apply p-6;
}

/* Enhanced Button Styles */
.btn-enhanced {
  @apply inline-flex items-center px-6 py-3 font-medium rounded-xl transition-all duration-200 focus:outline-none focus:ring-4;
}

.btn-enhanced-primary {
  @apply bg-gradient-to-r from-blue-600 to-indigo-600 text-white shadow-lg hover:from-blue-700 hover:to-indigo-700 transform hover:scale-105 focus:ring-blue-200;
}

.btn-enhanced-secondary {
  @apply border-2 border-gray-300 text-gray-700 hover:bg-gray-50 hover:border-gray-400 focus:ring-gray-200;
}

/* Enhanced Form Styles */
.form-enhanced {
  @apply w-full px-4 py-3 border border-gray-200 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all duration-200;
}

.form-enhanced-select {
  @apply w-full px-4 py-3 border border-gray-200 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all duration-200;
}

/* Enhanced Status Badges */
.status-badge {
  @apply inline-flex items-center px-2.5 py-1 rounded-full text-xs font-medium;
}

.status-pending {
  @apply bg-red-100 text-red-800;
}

.status-partial {
  @apply bg-yellow-100 text-yellow-800;
}

.status-paid {
  @apply bg-green-100 text-green-800;
}

/* Responsive Utilities */
@media (max-width: 640px) {
  .mobile-stack {
    @apply flex-col space-y-4;
  }
  
  .mobile-full {
    @apply w-full;
  }
  
  .mobile-text-sm {
    @apply text-xs;
  }
  
  .mobile-p-3 {
    @apply p-2;
  }
  
  .mobile-gap-2 {
    @apply gap-2;
  }
}

@media (max-width: 768px) {
  .tablet-stack {
    @apply flex-col space-y-4;
  }
  
  .tablet-text-base {
    @apply text-sm;
  }
  
  .tablet-p-4 {
    @apply p-3;
  }
}

/* Text truncation utilities */
.line-clamp-2 {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.line-clamp-3 {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

/* Mobile-First Card Styles */
.card-mobile {
  @apply rounded-xl border border-gray-200 bg-white;
}

.card-mobile-header {
  @apply p-3 sm:p-4 border-b border-gray-100;
}

.card-mobile-body {
  @apply p-3 sm:p-4;
}

.card-mobile-footer {
  @apply p-3 sm:p-4 border-t border-gray-100;
}

/* Mobile Button Styles */
.btn-mobile {
  @apply w-full sm:w-auto px-3 py-2 text-xs sm:text-sm font-medium rounded-lg transition-all duration-200;
}

.btn-mobile-primary {
  @apply bg-green-600 text-white hover:bg-green-700 focus:ring-2 focus:ring-green-200;
}

.btn-mobile-secondary {
  @apply bg-gray-100 text-gray-700 hover:bg-gray-200 focus:ring-2 focus:ring-gray-200;
}

.btn-mobile-danger {
  @apply bg-red-100 text-red-700 hover:bg-red-200 focus:ring-2 focus:ring-red-200;
}

/* Sales Card Mobile Optimizations */
.sales-card-mobile {
  @apply rounded-xl border border-gray-200 bg-white p-4 sm:p-6;
}

.sales-card-header-mobile {
  @apply mb-4;
}

.sales-card-details-mobile {
  @apply grid grid-cols-1 gap-4 mb-4;
}

.sales-card-actions-mobile {
  @apply pt-4 border-t border-gray-100;
}

.sales-card-button-grid {
  @apply grid grid-cols-3 gap-2;
}

.sales-card-button {
  @apply w-full inline-flex items-center justify-center px-2 sm:px-3 py-2 text-xs sm:text-sm font-medium rounded-lg transition-all duration-200;
}

/* Responsive Text Sizes */
.text-mobile-xs {
  @apply text-xs sm:text-sm;
}

.text-mobile-sm {
  @apply text-sm sm:text-base;
}

.text-mobile-base {
  @apply text-base sm:text-lg;
}

.text-mobile-lg {
  @apply text-lg sm:text-xl;
}

/* Responsive Spacing */
.space-mobile-2 {
  @apply space-y-2 sm:space-y-0;
}

.space-mobile-3 {
  @apply space-y-3 sm:space-y-0;
}

.space-mobile-4 {
  @apply space-y-4 sm:space-y-0;
}

/* Mobile Grid Layouts */
.grid-mobile-1 {
  @apply grid-cols-1;
}

.grid-mobile-2 {
  @apply grid-cols-2;
}

.grid-mobile-3 {
  @apply grid-cols-3;
}

@media (max-width: 640px) {
  .grid-mobile-2 {
    @apply grid-cols-1;
  }
  
  .grid-mobile-3 {
    @apply grid-cols-1;
  }
}

/* Enhanced Animations */
.hover-lift {
  @apply transition-transform duration-200 hover:-translate-y-1;
}

.hover-scale {
  @apply transition-transform duration-200 hover:scale-105;
}

.hover-glow {
  @apply transition-shadow duration-200 hover:shadow-xl;
}

/* Loading States */
.loading-pulse {
  @apply animate-pulse bg-gray-200;
}

.loading-shimmer {
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%);
  background-size: 200% 100%;
  animation: shimmer 1.5s infinite;
}

@keyframes shimmer {
  0% {
    background-position: -200% 0;
  }
  100% {
    background-position: 200% 0;
  }
}

/* Button Styles */
.btn {
  @apply inline-flex items-center px-4 py-2 border text-sm font-medium rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500;
}

.btn-primary {
  @apply border-transparent text-white bg-blue-600 hover:bg-blue-700 focus:ring-blue-500;
}

.btn-secondary {
  @apply border-gray-300 text-gray-700 bg-white hover:bg-gray-50 focus:ring-blue-500;
}

.btn-success {
  @apply border-transparent text-white bg-green-600 hover:bg-green-700 focus:ring-green-500;
}

.btn-info {
  @apply border-transparent text-white bg-blue-500 hover:bg-blue-600 focus:ring-blue-500;
}

.btn-warning {
  @apply border-transparent text-white bg-yellow-600 hover:bg-yellow-700 focus:ring-yellow-500;
}

.btn-danger {
  @apply border-transparent text-white bg-red-600 hover:bg-red-700 focus:ring-red-500;
}

.btn-outline {
  @apply border-gray-300 text-gray-700 bg-white hover:bg-gray-50 focus:ring-blue-500;
}

/* Form Styles */
.form-group {
  @apply mb-4;
}

.form-label {
  @apply block text-sm font-medium text-gray-700 mb-2;
}

.form-input {
  @apply block w-full border-gray-300 rounded-md shadow-sm focus:ring-blue-500 focus:border-blue-500 sm:text-sm;
}

.form-select {
  @apply block w-full border-gray-300 rounded-md shadow-sm focus:ring-blue-500 focus:border-blue-500 sm:text-sm;
}

.form-textarea {
  @apply block w-full border-gray-300 rounded-md shadow-sm focus:ring-blue-500 focus:border-blue-500 sm:text-sm;
}

/* Table Styles */
.table {
  @apply min-w-full divide-y divide-gray-200;
}

/* Report Table Styles */
.report-table {
  @apply min-w-full divide-y divide-gray-200 border border-gray-200 rounded-lg overflow-hidden shadow-sm;
}

.report-table thead {
  @apply bg-gradient-to-r from-gray-50 to-gray-100;
}

.report-table th {
  @apply px-6 py-3 text-left text-xs font-medium text-gray-600 uppercase tracking-wider;
}

.report-table td {
  @apply px-6 py-4 whitespace-nowrap text-sm text-gray-900;
}

.report-table tbody tr {
  @apply hover:bg-gray-50 transition-colors duration-150;
}

.report-table tbody tr:nth-child(even) {
  @apply bg-gray-50;
}

.report-table tbody tr:hover {
  @apply bg-blue-50;
}

/* Status Badge Styles */
.status-badge {
  @apply inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium;
}

.status-pending {
  @apply bg-yellow-100 text-yellow-800;
}

.status-partial {
  @apply bg-blue-100 text-blue-800;
}

.status-collected {
  @apply bg-green-100 text-green-800;
}

.status-paid {
  @apply bg-green-100 text-green-800;
}

.status-badge.status-pending {
  @apply bg-yellow-100 text-yellow-800;
}

.status-badge.status-partial {
  @apply bg-blue-100 text-blue-800;
}

.status-badge.status-collected {
  @apply bg-green-100 text-green-800;
}

.status-badge.status-paid {
  @apply bg-green-100 text-green-800;
}

/* Report Output Styles */
.report-output {
  @apply space-y-4;
}

.report-output h3 {
  @apply text-lg font-semibold text-gray-900 border-b border-gray-200 pb-2;
}

.report-output h4 {
  @apply text-base font-medium text-gray-800 mt-6 mb-3;
}

.report-output p {
  @apply text-sm text-gray-700;
}

.report-output .error {
  @apply text-red-600 bg-red-50 border border-red-200 rounded-lg p-3;
}

/* Tab Navigation Styles */
.tab-nav {
  @apply flex space-x-8 border-b border-gray-200;
}

.tab-btn {
  @apply py-2 px-1 border-b-2 font-medium text-sm transition-colors duration-200;
}

.tab-btn.active {
  @apply border-primary-500 text-primary-600;
}

.tab-btn:not(.active) {
  @apply border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300;
}

/* Fiscal Year Selector Styles */
.fiscal-year-selector {
  @apply mb-6;
}

.selected-fy-details {
  @apply p-4 bg-blue-50 border border-blue-200 rounded-lg;
}

.current-fiscal-year {
  @apply p-4 bg-green-50 border border-green-200 rounded-lg;
}

.no-fiscal-year {
  @apply p-4 bg-yellow-50 border border-yellow-200 rounded-lg;
}

.table-responsive {
  @apply overflow-x-auto border border-gray-200 rounded-lg;
}

.table-responsive .table {
  @apply min-w-full;
}

.table-header {
  @apply bg-gray-50;
}

.table-header-cell {
  @apply px-4 sm:px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider whitespace-nowrap;
}

.table-body {
  @apply bg-white divide-y divide-gray-200;
}

.table-row {
  @apply hover:bg-gray-50;
}

.table-cell {
  @apply px-4 sm:px-6 py-4 whitespace-nowrap text-sm text-gray-900;
}

/* Responsive Table Utilities - Fixed to avoid circular dependency */
.table-mobile-stack {
  display: block;
}

.table-mobile-stack .table-row {
  display: block;
  border-bottom: 1px solid #e5e7eb;
}

.table-mobile-stack .table-header {
  display: none;
}

.table-mobile-stack .table-header-cell {
  display: none;
}

.table-mobile-stack .table-body {
  display: block;
}

.table-mobile-stack .table-cell {
  display: block;
  text-align: left;
  padding: 0.5rem 1rem;
}

.table-mobile-stack .table-cell:before {
  content: '';
}

/* Mobile table cell labels */
@media (max-width: 640px) {
  .table-mobile-stack .table-cell:before {
    content: attr(data-label) ': ';
    font-weight: 500;
    color: #374151;
    margin-right: 0.5rem;
  }
  
  .table-mobile-stack .table-cell {
    border-bottom: 1px solid #f3f4f6;
    padding-bottom: 0.5rem;
    margin-bottom: 0.5rem;
  }
  
  .table-mobile-stack .table-row {
    margin-bottom: 1rem;
    padding-bottom: 1rem;
    border-bottom: 2px solid #e5e7eb;
  }
}

/* Responsive table utilities for larger screens */
@media (min-width: 640px) {
  .table-mobile-stack {
    display: table;
  }
  
  .table-mobile-stack .table-row {
    display: table-row;
    border-bottom: none;
  }
  
  .table-mobile-stack .table-header {
    display: table-header-group;
  }
  
  .table-mobile-stack .table-header-cell {
    display: table-cell;
  }
  
  .table-mobile-stack .table-body {
    display: table-row-group;
  }
  
  .table-mobile-stack .table-cell {
    display: table-cell;
    text-align: left;
    padding: 1rem 1.5rem;
  }
  
  .table-mobile-stack .table-cell:before {
    content: none;
  }
}

/* Status Badge Styles */
.status-badge {
  @apply inline-flex px-2 py-1 text-xs font-semibold rounded-full;
}

.status-pending {
  @apply bg-yellow-100 text-yellow-800;
}

.status-partial {
  @apply bg-blue-100 text-blue-800;
}

.status-paid, .status-collected {
  @apply bg-green-100 text-green-800;
}

.status-overdue {
  @apply bg-red-100 text-red-800;
}

/* Alert Styles */
.alert {
  @apply p-4 rounded-md;
}

.alert-success {
  @apply bg-green-50 border border-green-200 text-green-800;
}

.alert-error {
  @apply bg-red-50 border border-red-200 text-red-800;
}

.alert-warning {
  @apply bg-yellow-50 border border-yellow-200 text-yellow-800;
}

.alert-info {
  @apply bg-blue-50 border border-blue-200 text-blue-800;
}

/* Modal Styles */
.modal-overlay {
  @apply fixed inset-0 bg-gray-600 bg-opacity-75 z-50;
}

.modal {
  @apply fixed inset-0 z-50 overflow-y-auto;
}

.modal-content {
  @apply flex min-h-full items-end justify-center p-4 text-center sm:items-center sm:p-0;
}

.modal-panel {
  @apply relative transform overflow-hidden rounded-lg bg-white text-left shadow-xl transition-all sm:my-8 sm:w-full sm:max-w-lg;
}

.modal-header {
  @apply px-4 sm:px-6 py-4 border-b border-gray-200;
}

.modal-body {
  @apply px-4 sm:px-6 py-4;
}

.modal-footer {
  @apply px-4 sm:px-6 py-4 border-t border-gray-200 flex flex-col sm:flex-row justify-end space-y-2 sm:space-y-0 sm:space-x-3;
}

/* Responsive Utilities */
@media (max-width: 768px) {
  .mobile-hidden {
    display: none;
  }
  
  .mobile-block {
    display: block;
  }
  
  /* Mobile-specific table styles */
  .table-responsive {
    overflow-x: auto;
  }
  
  .table-responsive .table {
    min-width: 100%;
  }
  
  /* Mobile form improvements */
  .form-input, .form-select, .form-textarea {
    font-size: 1rem;
  }
  
  /* Mobile button improvements */
  .btn {
    width: 100%;
    justify-content: center;
  }
  
  /* Mobile card improvements */
  .card {
    margin-left: 0.5rem;
    margin-right: 0.5rem;
  }
  
  .card-header {
    padding: 1rem;
  }
  
  .card-body {
    padding: 1rem;
  }
}

@media (min-width: 769px) {
  .btn {
    width: auto;
  }
  
  .card {
    margin-left: 0;
    margin-right: 0;
  }
}

/* Tablet-specific styles */
@media (min-width: 769px) and (max-width: 1024px) {
  .tablet-hidden {
    display: none;
  }
  
  .tablet-block {
    display: block;
  }
  
  /* Tablet grid adjustments */
  .tablet-grid-2 {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
  
  .tablet-grid-3 {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
}

/* Desktop-specific styles */
@media (min-width: 1025px) {
  .desktop-hidden {
    display: none;
  }
  
  .desktop-block {
    display: block;
  }
  
  /* Desktop grid adjustments */
  .desktop-grid-4 {
    grid-template-columns: repeat(4, minmax(0, 1fr));
  }
  
  .desktop-grid-5 {
    grid-template-columns: repeat(5, minmax(0, 1fr));
  }
}

/* Animation Classes */
.fade-in {
  @apply animate-fade-in;
}

.slide-in-right {
  @apply animate-slide-up;
}

.slide-in-left {
  @apply animate-slide-up;
}

/* Custom Scrollbar */
.custom-scrollbar::-webkit-scrollbar {
  width: 6px;
}

.custom-scrollbar::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.1);
}

.custom-scrollbar::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.3);
  border-radius: 3px;
}

.custom-scrollbar::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 0, 0, 0.5);
}

/* Focus States */
.focus-ring {
  @apply focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500;
}

/* Hover Effects */
.hover-lift {
  @apply transition-transform duration-200 hover:-translate-y-1 hover:shadow-lg;
