/* Responsive improvements for modals and tables */
@media (max-width: 640px) {
  /* Mobile-first table improvements */
  .table-responsive {
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
  }
  
  /* Ensure tables don't break on small screens */
  table {
    min-width: 600px; /* Minimum width to prevent squishing */
  }
  
  /* Better spacing for mobile modals */
  .modal-content {
    margin: 0.5rem;
    padding: 1rem;
    max-height: calc(100vh - 1rem);
  }
  
  /* Stack grid items on mobile */
  .grid {
    grid-template-columns: 1fr !important;
  }
  
  /* Better button sizing on mobile */
  .btn {
    padding: 0.75rem 1rem;
    font-size: 0.875rem;
  }
}

/* Tablet improvements */
@media (min-width: 641px) and (max-width: 1024px) {
  .modal-content {
    margin: 1rem;
    padding: 1.5rem;
  }
  
  /* Two column layout on tablets */
  .grid-cols-1.md\:grid-cols-2 {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* Desktop improvements */
@media (min-width: 1025px) {
  .modal-content {
    margin: 2rem auto;
    padding: 2rem;
  }
}

/* Ensure modals are always scrollable */
.modal-content {
  overflow-y: auto;
  overflow-x: hidden;
}

/* Better table responsiveness */
.table-responsive {
  position: relative;
  width: 100%;
}

/* Horizontal scroll indicator for tables */
.table-responsive::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 20px;
  background: linear-gradient(to left, rgba(255,255,255,0.8), transparent);
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.3s;
}

.table-responsive:hover::after {
  opacity: 1;
}

/* Mobile-friendly form layouts */
@media (max-width: 640px) {
  .form-layout-desktop,
  .form-layout-desktop-enhanced {
    grid-template-columns: 1fr !important;
    gap: 1rem !important;
  }
  
  .form-column-left,
  .form-column-right {
    width: 100% !important;
  }
}

/* Ensure buttons are touch-friendly on mobile */
@media (max-width: 640px) {
  button, .btn {
    min-height: 44px; /* iOS touch target minimum */
    min-width: 44px;
  }
  
  /* Better spacing for mobile buttons */
  .btn + .btn {
    margin-left: 0.5rem;
  }
}

/* Modal backdrop improvements */
.modal-backdrop {
  backdrop-filter: blur(2px);
  -webkit-backdrop-filter: blur(2px);
}

/* Better focus states for accessibility */
.modal-content:focus-within {
  outline: 2px solid #3b82f6;
  outline-offset: 2px;
}

/* Smooth transitions for modal animations */
.modal-content {
  transition: all 0.3s ease-in-out;
  transform: translateY(0);
}

/* Loading states for better UX */
.loading-spinner {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

/* Responsive text sizing */
@media (max-width: 640px) {
  .text-lg { font-size: 1rem; }
  .text-xl { font-size: 1.125rem; }
  .text-2xl { font-size: 1.25rem; }
}

@media (min-width: 641px) {
  .text-lg { font-size: 1.125rem; }
  .text-xl { font-size: 1.25rem; }
  .text-2xl { font-size: 1.5rem; }
}

/* Better spacing for mobile */
@media (max-width: 640px) {
  .space-y-4 > * + * { margin-top: 1rem; }
  .space-y-6 > * + * { margin-top: 1.5rem; }
  .gap-4 { gap: 1rem; }
  .gap-6 { gap: 1.5rem; }
}

@media (min-width: 641px) {
  .space-y-4 > * + * { margin-top: 1.5rem; }
  .space-y-6 > * + * { margin-top: 2rem; }
  .gap-4 { gap: 1.5rem; }
  .gap-6 { gap: 2rem; }
}

/* Line clamp utilities for text truncation */
.line-clamp-1 {
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
}

.line-clamp-2 {
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}

.line-clamp-3 {
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
}

/* Enhanced warehouse card styles */
.warehouse-card {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.warehouse-card:hover {
  transform: translateY(-4px) scale(1.02);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

/* Responsive card grid improvements */
@media (max-width: 640px) {
  .warehouse-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .warehouse-card {
    margin-bottom: 1rem;
  }
}

@media (min-width: 641px) and (max-width: 1024px) {
  .warehouse-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
  }
}

@media (min-width: 1025px) {
  .warehouse-grid {
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

@media (min-width: 1280px) {
  .warehouse-grid {
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}

/* Card hover effects with better performance */
.warehouse-card-hover {
  will-change: transform, box-shadow;
  backface-visibility: hidden;
  transform: translateZ(0);
}

/* Smooth icon transitions */
.warehouse-icon {
  transition: all 0.2s ease-in-out;
}

.warehouse-card:hover .warehouse-icon {
  transform: scale(1.1);
}

/* Action button hover states */
.warehouse-action-btn {
  transition: all 0.2s ease-in-out;
}

.warehouse-action-btn:hover {
  transform: scale(1.1);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Mobile-first card spacing */
.warehouse-card-content {
  padding: 1rem;
}

@media (min-width: 640px) {
  .warehouse-card-content {
    padding: 1.25rem;
  }
}

@media (min-width: 1024px) {
  .warehouse-card-content {
    padding: 1.5rem;
  }
}

